.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  display: grid;
}

.photo-card {
  cursor: pointer;
  border-radius: 10px;
  transition: transform .2s ease-in-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
  vertical-align: middle;
}

.info {
  color: #fff;
  background-color: rgba(0, 0, 0, .8);
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.info-item {
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.info-item b {
  margin-right: 5px;
}

